import React, { useState, useEffect } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Button, Box, IconButton, Drawer, List, ListItem, ListItemText, Badge, useMediaQuery } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { useTheme } from '@mui/material/styles';
import './styles/Header.css';

const Header = ({ newBookingCount, clearNewBookings }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const token = localStorage.getItem('token');
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [drawerOpen, setDrawerOpen] = useState(false);

  useEffect(() => {
    if (newBookingCount > 0) {
      const badge = document.querySelector('.booking-badge');
      if (badge) {
        badge.classList.add('animate-badge');
        setTimeout(() => badge.classList.remove('animate-badge'), 1000); // Remove the animation class after 1 second
      }
    }
  }, [newBookingCount]);

  const handleLogout = () => {
    localStorage.removeItem('token');
    navigate('/login');
  };

  const handleDashboardClick = () => {
    clearNewBookings(); // Clear the new bookings count when navigating to dashboard
    navigate('/dashboard');
  };

  const toggleDrawer = (open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setDrawerOpen(open);
  };

  const menuItems = (
    <>
      {location.pathname !== '/dashboard' && location.pathname !== '/booking' && (
        <Button color="inherit" component={Link} to="/" sx={{ mr: 2 }}>
          Home
        </Button>
      )}
      <Button color="inherit" component={Link} to="/booking" sx={{ mr: 2 }}>
        Book an Appointment
      </Button>
      {token ? (
        <>
          <Box sx={{ position: 'relative', display: 'inline-block' }}>
            <Button color="inherit" onClick={handleDashboardClick} sx={{ mr: 2 }}>
              Dashboard
            </Button>
            {newBookingCount > 0 && (
              <Badge
                badgeContent={newBookingCount}
                color="secondary"
                className="booking-badge"
                sx={{
                  position: 'absolute',
                  top: isMobile ? 35 : -5,
                  left: '50%',
                  transform: 'translateX(-50%)',
                }}
              />
            )}
          </Box>
          <Button color="inherit" onClick={handleLogout}>
            Logout
          </Button>
        </>
      ) : (
        <>
          {location.pathname === '/login' ? (
            <Button color="inherit" component={Link} to="/signup">
              Sign Up
            </Button>
          ) : (
            <Button color="inherit" component={Link} to="/login">
              Login
            </Button>
          )}
        </>
      )}
    </>
  );

  return (
    <AppBar position="sticky">
      <Toolbar>
        <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
          <Box
            component="img"
            src="/images/dental-logo.svg"
            alt="Dental Booking Logo"
            sx={{ height: 40, width: 40, mr: 1 }}
          />
          <Typography variant="h6" component="div">
            Dental Booking
          </Typography>
        </Box>
        {isMobile ? (
          <>
            <IconButton
              color="inherit"
              edge="start"
              onClick={toggleDrawer(true)}
              sx={{ mr: 2 }}
            >
              <MenuIcon />
            </IconButton>
            <Drawer
              anchor="left"
              open={drawerOpen}
              onClose={toggleDrawer(false)}
            >
              <Box
                sx={{ width: 250 }}
                role="presentation"
                onClick={toggleDrawer(false)} // Close the drawer when an item is clicked
                onKeyDown={toggleDrawer(false)} // Close the drawer on keydown events
              >
                <List>
                  {location.pathname !== '/dashboard' && location.pathname !== '/booking' && (
                    <ListItem button component={Link} to="/">
                      <ListItemText primary="Home" />
                    </ListItem>
                  )}
                  <ListItem button component={Link} to="/booking">
                    <ListItemText primary="Book an Appointment" />
                  </ListItem>
                  {token ? (
                    <>
                      <ListItem button onClick={handleDashboardClick} sx={{ position: 'relative' }}>
                        <ListItemText primary="Dashboard" />
                        {newBookingCount > 0 && (
                          <Badge
                            badgeContent={newBookingCount}
                            color="secondary"
                            className="booking-badge"
                            sx={{
                              position: 'absolute',
                              top: 30,
                              left: '50%',
                              transform: 'translateX(-50%)',
                            }}
                          />
                        )}
                      </ListItem>
                      <ListItem button onClick={handleLogout}>
                        <ListItemText primary="Logout" />
                      </ListItem>
                    </>
                  ) : (
                    <>
                      {location.pathname === '/login' ? (
                        <ListItem button component={Link} to="/signup">
                          <ListItemText primary="Sign Up" />
                        </ListItem>
                      ) : (
                        <ListItem button component={Link} to="/login">
                          <ListItemText primary="Login" />
                        </ListItem>
                      )}
                    </>
                  )}
                </List>
              </Box>
            </Drawer>
          </>
        ) : (
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            {menuItems}
          </Box>
        )}
      </Toolbar>
    </AppBar>
  );
};

export default Header;
