import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Avatar,
  Box,
  Typography,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Card,
  CardContent,
  Divider,
  IconButton,
  Grid,
  Paper,
  Skeleton,
  TextField,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import Notification from '../components/Notification';
import EventIcon from '@mui/icons-material/Event';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import dayjs from 'dayjs';

const DashboardPage = ({ setNewBookingCount, setIsAuthenticated }) => {
  const [appointments, setAppointments] = useState([]);
  const [user, setUser] = useState(null);
  const [openRescheduleDialog, setOpenRescheduleDialog] = useState(false);
  const [openCancelDialog, setOpenCancelDialog] = useState(false);
  const [openDentistDetailsDialog, setOpenDentistDetailsDialog] = useState(false);
  const [selectedDentist, setSelectedDentist] = useState(null);
  const [availableSlots, setAvailableSlots] = useState([]);
  const [selectedSlot, setSelectedSlot] = useState('');
  const [selectedAppointmentId, setSelectedAppointmentId] = useState('');
  const [notification, setNotification] = useState({ open: false, message: '', severity: 'info' });
  const [currentPage, setCurrentPage] = useState(0);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();
  const [editName, setEditName] = useState(false);
  const [newName, setNewName] = useState('');
  const [openDeleteDialog, setOpenDeleteDialog] = useState(false);

  const appointmentsPerPage = 8;

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      navigate('/login');
      return;
    }

    const fetchUserData = async () => {
      try {
        setLoading(true); // Start loading state
        const config = {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        };

        const userResponse = await axios.get('/api/auth/me', config);
        const userData = userResponse.data;
        setUser(userData);
        setNewName(userData.name); // Set the name here

        const appointmentResponse = await axios.get('/api/appointments', config);
        const appointmentsData = appointmentResponse.data;

        const updatedAppointments = await Promise.all(
          appointmentsData.map(async (appt) => {
            const slotsResponse = await axios.get(`/api/dentists/${appt.dentist._id}/slots`, config);
            return {
              ...appt,
              dentist: {
                ...appt.dentist,
                availableSlots: slotsResponse.data,
              },
            }; // <-- Ensure the semicolon is here to terminate the return statement properly
          })
        );
        setAppointments(updatedAppointments);
        setNewBookingCount(0); // Reset booking count when entering Dashboard
      } catch (error) {
        console.error('Error fetching user data:', error.response?.data || error.message);
        setNotification({
          open: true,
          message: error.response?.data?.message || 'Failed to load data. Please try again.',
          severity: 'error',
        });
      } finally {
        setLoading(false); // End loading state
      }
    };

    fetchUserData();
  }, [navigate, setNewBookingCount]); // 'user' is no longer a dependency

  const handleAvatarUpload = async (e) => {
    const file = e.target.files[0];
    if (!file) return;
  
    const formData = new FormData();
    formData.append('avatar', file);
  
    const token = localStorage.getItem('token');
    const config = {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'multipart/form-data',
      },
    };
  
    try {
      await axios.put('/api/auth/me/avatar', formData, config);
      // Fetch the latest user info after updating the avatar
      const userResponse = await axios.get('/api/auth/me', config);
      const userData = userResponse.data;
  
      setUser(userData); // Update the user state with the latest data
  
      setNotification({ open: true, message: 'Avatar updated successfully', severity: 'success' });
    } catch (error) {
      console.error('Error uploading avatar:', error.response?.data || error.message);
      setNotification({ open: true, message: error.response?.data?.message || 'Failed to upload avatar', severity: 'error' });
    }
  };

  const handleNameChange = async () => {
    if (!newName.trim() || newName === user.name) {
      setEditName(false);
      return;
    }
  
    const token = localStorage.getItem('token');
    const config = {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    };
  
    try {
      // Log the data being sent
      const updateData = { 
        name: newName, 
        phone: user.phone, // Ensure phone is included
        email: user.email 
      };
      console.log(user.phone)
      console.log("Sending update data:", updateData);
  
      await axios.put('/api/auth/me', updateData, config);
  
      // Fetch the latest user info after updating the name
      const userResponse = await axios.get('/api/auth/me', config);
      const userData = userResponse.data;
  
      setUser(userData); // Update the user state with the latest data
  
      setNotification({ open: true, message: 'Name updated successfully', severity: 'success' });
    } catch (error) {
      console.error('Error updating name:', error.response?.data || error.message);
      setNotification({ open: true, message: error.response?.data?.message || 'Failed to update name', severity: 'error' });
    } finally {
      setEditName(false);
    }
  };
  
    

  const handleCancelNameChange = () => {
    setNewName(user.name); // Revert to the original name
    setEditName(false);
  };

  const handleNameKeyPress = (event) => {
    if (event.key === 'Enter') {
      handleNameChange();
    }
  };

  const handleDeleteAccount = async () => {
    const token = localStorage.getItem('token');
    const config = {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    };

    try {
      await axios.delete('/api/auth/me', config);
      localStorage.removeItem('token'); // Remove the token
      setIsAuthenticated(false); // Update authentication state
      navigate('/'); // Redirect to home after deletion
    } catch (error) {
      console.error('Error deleting account:', error);
      setNotification({ open: true, message: 'Failed to delete account', severity: 'error' });
    }
  };

  const handleCancel = async (id) => {
    try {
      const token = localStorage.getItem('token');
      const config = {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      };
      await axios.delete(`/api/appointments/${id}`, config);
      setAppointments(appointments.filter((appt) => appt._id !== id));
      setNotification({ open: true, message: 'Appointment cancelled successfully', severity: 'success' });
      setOpenCancelDialog(false);
    } catch (error) {
      console.error('Error cancelling appointment:', error);
      setNotification({ open: true, message: 'Failed to cancel appointment', severity: 'error' });
    }
  };

  const handleOpenRescheduleDialog = async (appointment) => {
    const token = localStorage.getItem('token');
    try {
      const response = await axios.get(`/api/dentists/${appointment.dentist._id}/slots`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      const formattedSlots = response.data.map(slot => ({
        _id: slot._id,
        time: dayjs(slot.date).format('hh:mm A'),
        isoTime: slot.date,
      }));

      setAvailableSlots(formattedSlots);
      setSelectedAppointmentId(appointment._id);
      setOpenRescheduleDialog(true);
    } catch (error) {
      console.error('Error fetching slots:', error);
      setNotification({ open: true, message: 'Failed to fetch available slots', severity: 'error' });
    }
  };

  const handleReschedule = async () => {
    try {
      const token = localStorage.getItem('token');
      const config = {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      };

      const selectedSlotData = availableSlots.find(slot => slot.time === selectedSlot);
      if (!selectedSlotData) {
        setNotification({
          open: true,
          message: 'The selected slot is not available. Please choose a different time.',
          severity: 'error',
        });
        return;
      }

      const selectedSlotISO = selectedSlotData.isoTime;

      const response = await axios.put(
        `/api/appointments/${selectedAppointmentId}`,
        {
          time: selectedSlotISO,
          date: selectedSlotISO,
        },
        config
      );

      const updatedAppointment = response.data.appointment;

      try {
        const dentistResponse = await axios.get(`/api/dentists/${updatedAppointment.dentist}`, config);
        const dentist = dentistResponse.data;

        setAppointments(prevAppointments =>
          prevAppointments.map(appt =>
            appt._id === updatedAppointment._id ? { ...updatedAppointment, dentist } : appt
          )
        );

        setNotification({
          open: true,
          message: `Appointment successfully rescheduled to ${dayjs(updatedAppointment.time).format('MMMM D, YYYY [at] h:mm A')}`,
          severity: 'success',
        });
      } catch (dentistError) {
        console.error('Error fetching dentist:', dentistError.response?.data || dentistError.message);
        setNotification({
          open: true,
          message: 'Failed to retrieve updated dentist information. Please refresh the page.',
          severity: 'error',
        });
      }

      setOpenRescheduleDialog(false);
    } catch (error) {
      if (error.response?.status === 404) {
        console.error('Dentist not found:', error.response.data);
        setNotification({
          open: true,
          message: 'Dentist not found. Please try again later.',
          severity: 'error',
        });
      } else {
        console.error('Error rescheduling appointment:', error.response?.data || error.message);
        setNotification({
          open: true,
          message: 'Failed to reschedule appointment. Please try again.',
          severity: 'error',
        });
      }
    }
  };

  const handleOpenCancelDialog = (id) => {
    setSelectedAppointmentId(id);
    setOpenCancelDialog(true);
  };

  const handleCloseRescheduleDialog = () => {
    setOpenRescheduleDialog(false);
    setSelectedSlot('');
  };

  const handleCloseCancelDialog = () => {
    setOpenCancelDialog(false);
  };

  const handleNotificationClose = () => {
    setNotification({ ...notification, open: false });
  };

  const handleAvatarClick = (dentist) => {
    setSelectedDentist(dentist);
    setOpenDentistDetailsDialog(true);
  };

  const handleCloseDentistDetailsDialog = () => {
    setOpenDentistDetailsDialog(false);
    setSelectedDentist(null);
  };

  const totalPages = Math.ceil(appointments.length / appointmentsPerPage);

  const handleNextPage = () => {
    if (currentPage < totalPages - 1) {
      setCurrentPage(currentPage + 1);
    }
  };

  const handlePreviousPage = () => {
    if (currentPage > 0) {
      setCurrentPage(currentPage - 1);
    }
  };

  const displayedAppointments = appointments.slice(
    currentPage * appointmentsPerPage,
    (currentPage + 1) * appointmentsPerPage
  );

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: { xs: 'column', md: 'row' },
        height: 'calc(100vh - 64px)',
      }}
    >
      {/* User Info */}
      <Paper
      sx={{
        width: { xs: '100%', md: '25%' },
        p: 4,
        bgcolor: 'background.default',
        boxShadow: 4,
        borderRadius: 3,
        textAlign: 'center',
        mb: { xs: 0, md: 0 },
        mr: { md: 3 },
        position: { xs: 'fixed', md: 'sticky' },
        top: { xs: 64, md: 64 },
        zIndex: 1,
        padding: '20px',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',  // Center items horizontally
        justifyContent: 'center' // Center items vertically
      }}
    >
        {user ? (
          <>
            <Avatar sx={{ width: 100, height: 100, mx: 'auto', mb: 2 }} src={user.avatarUrl || '/images/default-avatar.svg'} alt={user.name} />
            <input
              type="file"
              accept="image/*"
              style={{ display: 'none' }}
              id="avatar-upload"
              onChange={handleAvatarUpload}
            />
            <label htmlFor="avatar-upload">
              <Button variant="outlined" component="span">
                Upload Picture
              </Button>
            </label>
            {editName ? (
              <>
                <TextField value={newName} onChange={(e) => setNewName(e.target.value)} onKeyPress={handleNameKeyPress} autoFocus fullWidth />
                <Box sx={{ mt: 2, display: 'flex', justifyContent: 'center', gap: 1 }}>
                  <Button variant="contained" color="primary" onClick={handleNameChange}>OK</Button>
                  <Button variant="outlined" color="secondary" onClick={handleCancelNameChange}>Cancel</Button>
                </Box>
              </>
            ) : (
              <>
                <Typography variant="h6" gutterBottom>
                  {user.name}
                  <IconButton onClick={() => setEditName(true)}>
                    <EditIcon />
                  </IconButton>
                </Typography>
              </>
            )}
            <Typography variant="body2" color="textSecondary" gutterBottom>
              {user.email}
            </Typography>
            {user.phone && (
              <Typography variant="body2" color="textSecondary" gutterBottom>
                Phone: {user.phone}
              </Typography>
            )}
            <Typography variant="body2" color="textSecondary">
              Joined: {new Date(user.createdAt).toLocaleDateString()}
            </Typography>
            <Button
              variant="contained"
              color="secondary"
              startIcon={<DeleteIcon />}
              onClick={() => setOpenDeleteDialog(true)}>
              Delete Account
            </Button>
            <Dialog open={openDeleteDialog} onClose={() => setOpenDeleteDialog(false)}>
              <DialogTitle>Delete Account</DialogTitle>
              <DialogContent>
                <DialogContentText>Are you sure you want to delete your account? This action is irreversible.</DialogContentText>
              </DialogContent>
              <DialogActions>
                <Button onClick={() => setOpenDeleteDialog(false)} color="secondary">
                  Cancel
                </Button>
                <Button onClick={handleDeleteAccount} color="primary">
                  Delete
                </Button>
              </DialogActions>
            </Dialog>
          </>
        ) : (
          <Box>
            <Skeleton variant="circular" width={100} height={100} sx={{ mx: 'auto', mb: 2 }} />
            <Skeleton variant="text" width="60%" sx={{ mx: 'auto', mb: 2 }} />
            <Skeleton variant="text" width="40%" sx={{ mx: 'auto', mb: 2 }} />
            <Skeleton variant="rectangular" width="80%" height={40} sx={{ mx: 'auto', mb: 2 }} />
          </Box>
        )}
      </Paper>

      {/* Appointments List */}
      <Box
        sx={{
          flex: 1,
          mt: { xs: '240px', md: 0 },
          overflowY: 'auto',
          px: { xs: 2, md: 4 }, // Add some padding on mobile and desktop
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',  // Center items horizontally
        }}>
        <Box sx={{ mb: 3 }}>
          <Typography variant="h4" gutterBottom>
            Your Appointments
          </Typography>
        </Box>
        <Grid container spacing={3}>
          {loading ? (
            Array.from(new Array(appointmentsPerPage)).map((_, index) => (
              <Grid item xs={12} sm={6} md={4} key={index}>
                <Skeleton variant="rectangular" height={200} />
              </Grid>
            ))
          ) : displayedAppointments.length === 0 ? (
            <Typography> No appointments found.</Typography>
          ) : (
            displayedAppointments.map((appt) => (
              <Grid item xs={12} sm={6} md={4} key={appt._id}>
                <Card
                  sx={{
                    boxShadow: 4,
                    borderRadius: 3,
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'space-between',
                    height: '100%',
                  }}
                >
                  <CardContent sx={{ p: 3 }}>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                      <Avatar
                        sx={{ cursor: 'pointer', mr: 2 }}
                        src={appt.dentist.avatarUrl || '/images/default-avatar.svg'}
                        alt={appt.dentist.name}
                        onClick={() => handleAvatarClick(appt.dentist)}
                      />
                      <Typography variant="h6">
                        Appointment with {appt.dentist.name}
                      </Typography>
                    </Box>
                    <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center', mb: 0.5 }}>
                      <EventIcon sx={{ mr: 1 }} />
                      {new Date(appt.date).toLocaleDateString()}
                    </Typography>
                    <Typography variant="body2" sx={{ display: 'flex', alignItems: 'center' }}>
                      <AccessTimeIcon sx={{ mr: 1 }} />
                      {dayjs(appt.time).format('hh:mm A')}
                    </Typography>
                  </CardContent>
                  <Divider sx={{ width: '100%' }} />
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', p: 3 }}>
                    {appt.dentist.availableSlots?.length > 1 && (
                      <Button
                        variant="outlined"
                        color="primary"
                        onClick={() => handleOpenRescheduleDialog(appt)}
                      >
                        Reschedule
                      </Button>
                    )}
                    <Button
                      variant="outlined"
                      color="secondary"
                      onClick={() => handleOpenCancelDialog(appt._id)}
                    >
                      Cancel
                    </Button>
                  </Box>
                </Card>
              </Grid>
            ))
          )}
        </Grid>

        {/* Pagination Controls */}
        {totalPages > 1 && (
          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 4 }}>
            <IconButton onClick={handlePreviousPage} disabled={currentPage === 0}>
              <ArrowBackIosIcon />
            </IconButton>
            <Typography variant="body2" sx={{ mx: 2, lineHeight: '32px' }}>
              Page {currentPage + 1} of {totalPages}
            </Typography>
            <IconButton onClick={handleNextPage} disabled={currentPage === totalPages - 1}>
              <ArrowForwardIosIcon />
            </IconButton>
          </Box>
        )}

        {/* Reschedule Dialog */}
        <Dialog open={openRescheduleDialog} onClose={handleCloseRescheduleDialog}>
          <DialogTitle>Reschedule Appointment</DialogTitle>
          <DialogContent>
            <DialogContentText>Select a new time slot for your appointment.</DialogContentText>
            <FormControl fullWidth sx={{ mt: 2 }}>
              <InputLabel>Select Time Slot</InputLabel>
              <Select
                value={selectedSlot}
                onChange={(e) => setSelectedSlot(e.target.value)}
              >
                {availableSlots.map((slot) => (
                  <MenuItem key={slot._id} value={slot.time}>
                    {slot.time}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseRescheduleDialog} color="secondary">
              Cancel
            </Button>
            <Button onClick={handleReschedule} color="primary" disabled={!selectedSlot}>
              Reschedule
            </Button>
          </DialogActions>
        </Dialog>

        {/* Cancel Confirmation Dialog */}
        <Dialog open={openCancelDialog} onClose={handleCloseCancelDialog}>
          <DialogTitle>Cancel Appointment</DialogTitle>
          <DialogContent>
            <DialogContentText>Are you sure you want to cancel this appointment?</DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseCancelDialog} color="secondary">
              No
            </Button>
            <Button onClick={() => handleCancel(selectedAppointmentId)} color="primary">
              Yes, Cancel
            </Button>
          </DialogActions>
        </Dialog>

        {/* Dentist Details Dialog */}
        <Dialog open={openDentistDetailsDialog} onClose={handleCloseDentistDetailsDialog}>
          <DialogTitle>Dentist Details</DialogTitle>
          <DialogContent>
            {selectedDentist ? (
              <Box>
                <Avatar
                  sx={{ width: 100, height: 100, mb: 2 }}
                  src={selectedDentist.avatarUrl || '/images/default-avatar.svg'}
                  alt={selectedDentist.name}
                />
                <Typography variant="h6">{selectedDentist.name}</Typography>
                <Typography variant="body2" color="textSecondary">
                  Specialty: {selectedDentist.specialty || 'General Dentistry'}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  Experience: {selectedDentist.experience || 'Not specified'} years
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  Phone: {selectedDentist.phone || 'Not specified'}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  Email: {selectedDentist.email || 'Not specified'}
                </Typography>
              </Box>
            ) : (
              <Typography>Loading dentist details...</Typography>
            )}
          </DialogContent>
          <DialogActions>
            <Button onClick={handleCloseDentistDetailsDialog} color="primary">
              Close
            </Button>
          </DialogActions>
        </Dialog>
      </Box>

      {/* Notification */}
      <Notification
        open={notification.open}
        message={notification.message}
        severity={notification.severity}
        onClose={handleNotificationClose}
      />
    </Box>
  );
};

export default DashboardPage;
