import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Avatar,
  Box,
  Typography,
  Button,
  TextField,
  Grid,
  Card,
  CardActionArea,
  CardContent,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Pagination,
  Skeleton,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import Notification from '../components/Notification';
import dayjs from 'dayjs';

const BookingPage = ({ setNewBookingCount }) => {
  const [dentists, setDentists] = useState([]);
  const [selectedDentist, setSelectedDentist] = useState(null);
  const [availableSlots, setAvailableSlots] = useState([]);
  const [selectedSlot, setSelectedSlot] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(true);
  const [loading, setLoading] = useState(true);
  const [notification, setNotification] = useState({ open: false, message: '', severity: 'info', actionText: '', onAction: null });
  const [searchQuery, setSearchQuery] = useState('');
  const [openModal, setOpenModal] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const dentistsPerPage = 8;
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (!token) {
      setIsAuthenticated(false);
      setNotification({
        open: true,
        message: 'You need to log in to book an appointment.',
        severity: 'warning',
        actionText: 'LOGIN',
        onAction: () => navigate('/login'),
      });
      setLoading(false);
      return;
    }

    const fetchDentists = async () => {
      try {
        const response = await axios.get('/api/dentists', {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        const dentistsData = await Promise.all(
          response.data.map(async (dentist) => {
            const slotsResponse = await axios.get(`/api/dentists/${dentist._id}/slots`, {
              headers: {
                Authorization: `Bearer ${token}`, // Include the Authorization header
              },
            });
            const formattedSlots = slotsResponse.data.map(slot => ({
              _id: slot._id,
              dateTime: dayjs(slot.date).format('MMMM D, YYYY [at] hh:mm A'),
              isoTime: new Date(slot.date).toISOString(),
            }));
            return { ...dentist, availableSlots: formattedSlots };
          })
        );
        

        setDentists(dentistsData);
      } catch (error) {
        console.error('Error fetching dentists:', error);
        if (error.response && error.response.status === 401) {
          localStorage.removeItem('token');
          setIsAuthenticated(false);
          setNotification({
            open: true,
            message: 'Session expired. Please log in again.',
            severity: 'warning',
            actionText: 'LOGIN',
            onAction: () => navigate('/login'),
          });
        } else {
          setNotification({
            open: true,
            message: 'Failed to load dentists. Please try again later.',
            severity: 'error',
          });
        }
      } finally {
        setLoading(false);
      }
    };

    fetchDentists();
  }, [navigate]);

  const handleBooking = async (e) => {
    e.preventDefault();  // Prevent default form submission behavior, which causes a full page reload
  
    if (!isAuthenticated) {
      setNotification({
        open: true,
        message: 'You need to log in to book an appointment.',
        severity: 'warning',
        actionText: 'LOGIN',
        onAction: () => navigate('/login'),
      });
      return;
    }
  
    try {
      const token = localStorage.getItem('token');
      const config = {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      };
  
      const selectedSlotData = availableSlots.find(slot => slot.dateTime === selectedSlot);
      const selectedSlotISO = selectedSlotData?.isoTime;
  
      const response = await axios.post(
        '/api/appointments',
        {
          dentistId: selectedDentist._id,
          time: selectedSlotISO,
          date: new Date(selectedSlotISO),
        },
        config
      );
  
      const appointment = response.data.appointment;
      const dentistName = appointment?.dentist?.name;
      const formattedTime = dayjs(appointment.time).format('MMMM D, YYYY [at] h:mm A');
  
      setNotification({
        open: true,
        message: `Appointment booked with ${dentistName} on ${formattedTime}`,
        severity: 'success',
      });
  
      setNewBookingCount(prevCount => prevCount + 1); // Increment the booking count
      setOpenModal(false);
    } catch (error) {
      console.error('Error booking appointment:', error.response?.data || error.message);
      const errorMessage = error.response?.data?.message || 'Failed to book appointment. Please try again.';
      setNotification({
        open: true,
        message: errorMessage,
        severity: 'error',
      });
    }
  };
  
  

  const handleNotificationClose = () => {
    setNotification({ ...notification, open: false });
  };

  const handleOpenModal = (dentist) => {
    if (dentist._id !== selectedDentist?._id) {
      setAvailableSlots([]);
    }
    setSelectedDentist(dentist);
    setAvailableSlots(dentist.availableSlots);
    setOpenModal(true);
  };

  const handleCloseModal = () => {
    setOpenModal(false);
    setSelectedSlot('');
  };

  const filteredDentists = dentists.filter(dentist =>
    dentist.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const indexOfLastDentist = currentPage * dentistsPerPage;
  const indexOfFirstDentist = indexOfLastDentist - dentistsPerPage;
  const currentDentists = filteredDentists.slice(indexOfFirstDentist, indexOfLastDentist);

  const handlePageChange = (event, value) => {
    setCurrentPage(value);
  };

  return (
    <Box
      sx={{
        display: 'flex',
        height: 'calc(100vh - 64px)',
        bgcolor: 'background.default',
        flexDirection: 'column',
      }}
    >
      <Box sx={{ p: 3 }}>
        <TextField
          label="Search Dentist"
          variant="outlined"
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          fullWidth
        />
      </Box>

      {loading ? (
        <Grid container spacing={2} sx={{ p: 3, flexGrow: 1, overflowY: 'auto' }}>
          {Array.from(new Array(dentistsPerPage)).map((_, index) => (
            <Grid item xs={12} sm={6} md={4} lg={3} key={index}>
              <Card>
                <Skeleton variant="rectangular" height={150} />
                <CardContent>
                  <Skeleton variant="text" />
                  <Skeleton variant="text" width="60%" />
                  <Skeleton variant="rectangular" height={30} sx={{ mt: 2 }} />
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      ) : (
        <Grid container spacing={2} sx={{ p: 3, flexGrow: 1, overflowY: 'auto' }}>
          {currentDentists.map((dentist) => (
            <Grid item xs={12} sm={6} md={4} lg={3} key={dentist._id}>
              <Card>
                <CardActionArea onClick={() => handleOpenModal(dentist)}>
                  <Box sx={{ display: 'flex', alignItems: 'center', p: 2 }}>
                    <Avatar
                      src={dentist.avatar || '/images/default-avatar.svg'}
                      alt={dentist.name}
                      sx={{ width: 64, height: 64, mr: 2 }}
                    />
                    <Box>
                      <Typography variant="h6">{dentist.name}</Typography>
                      <Typography variant="body2" color="textSecondary">{dentist.specialization}</Typography>
                    </Box>
                  </Box>
                </CardActionArea>
                <CardContent sx={{ textAlign: 'center' }}>
                  <Typography variant="body2" color="textSecondary">
                    Available Slots:
                  </Typography>
                  {dentist.availableSlots.length > 0 ? (
                    dentist.availableSlots.slice(0, 3).map((slot) => (
                      <Typography key={slot._id} variant="body2">
                        {slot.dateTime}
                      </Typography>
                    ))
                  ) : (
                    <Typography variant="body2" color="textSecondary">No slots available</Typography>
                  )}
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => handleOpenModal(dentist)}
                    sx={{ mt: 2 }}
                    aria-label={`Book an appointment with ${dentist.name}`}
                    aria-hidden={openModal ? "true" : undefined}
                  >
                    Book an Appointment
                  </Button>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      )}

      {!loading && (
        <Box sx={{ display: 'flex', justifyContent: 'center', p: 2 }}>
          <Pagination
            count={Math.ceil(filteredDentists.length / dentistsPerPage)}
            page={currentPage}
            onChange={handlePageChange}
            color="primary"
          />
        </Box>
      )}

      <Dialog open={openModal} onClose={handleCloseModal} maxWidth="sm" fullWidth>
        <DialogTitle>{selectedDentist?.name}</DialogTitle>
        <DialogContent>
          {selectedDentist && (
            <>
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <Avatar
                  src={selectedDentist.avatar || '/images/default-avatar.svg'}
                  alt={selectedDentist.name}
                  sx={{ width: 80, height: 80, mr: 2 }}
                />
                <Box>
                  <Typography variant="h6">{selectedDentist.name}</Typography>
                  <Typography variant="body2" color="textSecondary">{selectedDentist.specialization}</Typography>
                </Box>
              </Box>
              <Typography variant="body1" sx={{ mb: 2 }}>
                {selectedDentist.bio || "No bio available."}
              </Typography>
              <Typography variant="h6" sx={{ mb: 1 }}>
                Available Slots
              </Typography>
              <FormControl fullWidth sx={{ mt: 2 }}>
                <InputLabel>Select Date and Time Slot</InputLabel>
                <Select
                  value={selectedSlot || ''}
                  onChange={(e) => setSelectedSlot(e.target.value)}
                >
                  {availableSlots.length > 0 ? (
                    availableSlots.map((slot) => (
                      <MenuItem key={slot._id} value={slot.dateTime}>
                        {slot.dateTime}
                      </MenuItem>
                    ))
                  ) : (
                    <MenuItem disabled>No slots available</MenuItem>
                  )}
                </Select>
              </FormControl>
            </>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseModal} color="secondary">
            Cancel
          </Button>
          <Button
            variant="contained"
            color="primary"
            onClick={handleBooking}
            disabled={!selectedSlot}
          >
            Book Appointment
          </Button>
        </DialogActions>
      </Dialog>

      <Notification
        open={notification.open}
        message={notification.message}
        severity={notification.severity}
        onClose={handleNotificationClose}
        actionText={notification.actionText}
        onAction={notification.onAction}
      />
    </Box>
  );
};

export default BookingPage;
