import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Box, Typography, TextField, Button, IconButton, InputAdornment, CircularProgress } from '@mui/material';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';
import Notification from '../components/Notification';

const ResetPasswordPage = () => {
  const { token } = useParams();
  const { register, handleSubmit, formState: { errors }, watch, reset } = useForm();
  const [notification, setNotification] = useState({ open: false, message: '', severity: 'info' });
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const navigate = useNavigate();
  const [resetToken, setResetToken] = useState('');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (token) {
      setResetToken(token);
      localStorage.setItem('resetToken', token); // Store raw token in localStorage
      window.history.replaceState({}, document.title, '/reset-password');
    } else {
      const storedToken = localStorage.getItem('resetToken');
      if (storedToken) {
        setResetToken(storedToken); // Use token from localStorage
      }
    }
  }, [token]);

  const handleClickShowPassword = () => setShowPassword(!showPassword);
  const handleClickShowConfirmPassword = () => setShowConfirmPassword(!showConfirmPassword);

  const onSubmitResetPassword = async (data) => {
    setLoading(true);
    try {
      await axios.post('/api/auth/reset-password', { token: resetToken, password: data.password });
      setNotification({ open: true, message: 'Password reset successful! Redirecting...', severity: 'success' });
      setTimeout(() => {
        localStorage.removeItem('resetToken');
        navigate('/login');
      }, 2000);
    } catch (error) {
      if (error.response && error.response.status === 400) {
        setNotification({ open: true, message: 'Invalid or expired token. Redirecting to request a new link...', severity: 'error' });
        setTimeout(() => {
          navigate('/reset-password');
        }, 2000);
      } else {
        setNotification({ open: true, message: error.response?.data?.message || 'Error resetting password', severity: 'error' });
      }
    } finally {
      setLoading(false);
    }
  };
  

  const onSubmitForgotPassword = async (data) => {
    setLoading(true); // Set loading to true when submission starts
    try {
      await axios.post('/api/auth/forgot-password', data);
      setNotification({ open: true, message: 'Password reset link sent! Please check your email, including the spam folder.', severity: 'success' });
      reset(); // Clear the form inputs
    } catch (error) {
      setNotification({ open: true, message: error.response?.data?.message || 'Error sending reset link', severity: 'error' });
    } finally {
      setLoading(false); // Set loading to false when submission is done
    }
  };
  

  const handleNotificationClose = () => {
    setNotification({ ...notification, open: false });
  };

  const password = watch('password');
  
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        height: 'calc(100vh - 64px)',
        px: 3,
        py: 6,
        bgcolor: 'background.default',
      }}
    >
      <Box
        sx={{
          maxWidth: 400,
          mx: 'auto',
          p: 3,
          bgcolor: 'background.paper',
          boxShadow: 3,
          borderRadius: 2,
        }}
      >
        <Typography variant="h4" gutterBottom>
          {resetToken ? 'Reset Password' : 'Forgot Password'}
        </Typography>
        <form onSubmit={handleSubmit(resetToken ? onSubmitResetPassword : onSubmitForgotPassword)}>
          {!resetToken && (
            <TextField
              label="Email"
              type="email"
              variant="outlined"
              fullWidth
              margin="normal"
              {...register('email', { required: 'Email is required' })}
              error={!!errors.email}
              helperText={errors.email ? errors.email.message : ''}
            />
          )}
          {resetToken && (
            <>
              <TextField
                label="New Password"
                type={showPassword ? 'text' : 'password'}
                variant="outlined"
                fullWidth
                margin="normal"
                {...register('password', { required: 'Password is required' })}
                error={!!errors.password}
                helperText={errors.password ? errors.password.message : ''}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton onClick={handleClickShowPassword} edge="end">
                        {showPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                label="Confirm Password"
                type={showConfirmPassword ? 'text' : 'password'}
                variant="outlined"
                fullWidth
                margin="normal"
                {...register('confirmPassword', {
                  required: 'Please confirm your password',
                  validate: value => value === password || 'Passwords do not match',
                })}
                error={!!errors.confirmPassword}
                helperText={errors.confirmPassword ? errors.confirmPassword.message : ''}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton onClick={handleClickShowConfirmPassword} edge="end">
                        {showConfirmPassword ? <VisibilityOff /> : <Visibility />}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
            </>
          )}
          <Button
            type="submit"
            variant="contained"
            color="primary"
            fullWidth
            sx={{ mt: 2 }}
            disabled={loading} // Disable button while loading
            >
            {loading ? <CircularProgress size={24} color="inherit" /> : (resetToken ? 'Reset Password' : 'Send Reset Link')}
            </Button>


        </form>
        <Notification
          open={notification.open}
          message={notification.message}
          severity={notification.severity}
          onClose={handleNotificationClose}
        />
      </Box>
    </Box>
  );
};


export default ResetPasswordPage;
