import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import HomePage from './pages/HomePage';
import BookingPage from './pages/BookingPage';
import DashboardPage from './pages/DashboardPage';
import LoginPage from './pages/LoginPage';
import SignUpPage from './pages/SignUpPage';
import Header from './components/Header';
import ProtectedRoute from './components/ProtectedRoute';
import { setupAxiosInterceptor } from './services/axiosConfig';
import ResetPasswordPage from './pages/ResetPasswordPage';
import NotFoundPage from './pages/NotFoundPage';

const App = () => {
  const [newBookingCount, setNewBookingCount] = useState(0);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    setupAxiosInterceptor();
    const token = localStorage.getItem('token');
    setIsAuthenticated(!!token); // Set authenticated state based on presence of token
  }, []);

  const clearNewBookings = () => {
    setNewBookingCount(0); // Reset the new bookings count
  };

  return (
    <Router>
      <Header newBookingCount={newBookingCount} clearNewBookings={clearNewBookings} />
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route 
          path="/booking" 
          element={<ProtectedRoute element={() => <BookingPage setNewBookingCount={setNewBookingCount} />} />} 
        />
        <Route 
          path="/dashboard" 
          element={<ProtectedRoute element={() => <DashboardPage setNewBookingCount={setNewBookingCount} setIsAuthenticated={setIsAuthenticated}/>} />} 
        />
        {/* Public routes */}
        {!isAuthenticated && <Route path="/login" element={<LoginPage />} />}
        {!isAuthenticated && <Route path="/signup" element={<SignUpPage />} />}
        
        {/* Redirect authenticated users away from login/signup */}
        {isAuthenticated && <Route path="/login" element={<Navigate to="/dashboard" replace />} />}
        {isAuthenticated && <Route path="/signup" element={<Navigate to="/dashboard" replace />} />}

        {/* Reset Password */}
        <Route path="/reset-password/:token" element={<ResetPasswordPage />} />
        <Route path="/reset-password" element={<ResetPasswordPage />} />
        
        {/* 404 - Not Found */}
        <Route path="*" element={<NotFoundPage />} />
      </Routes>
    </Router>
  );
};

export default App;
