import React, { useState } from 'react';
import axios from 'axios';
import { useForm } from 'react-hook-form';
import { Box, Typography, TextField, Button, IconButton, InputAdornment, useMediaQuery, useTheme, CircularProgress } from '@mui/material';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import Notification from '../components/Notification';
import PhoneInput from 'react-phone-input-2';
import 'react-phone-input-2/lib/style.css';

const LoginPage = () => {
  const { register, handleSubmit, formState: { errors }, setValue } = useForm();
  const [loading, setLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [notification, setNotification] = useState({ open: false, message: '', severity: 'info' });
  const [usePhone, setUsePhone] = useState(false); // Toggle between phone and email
  const navigate = useNavigate();
  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.up('md'));

  const handleClickShowPassword = () => setShowPassword(!showPassword);

  const onSubmit = async (data) => {
    setLoading(true);
    try {
      const response = await axios.post('/api/auth/login', data);
      localStorage.setItem('token', response.data.token);
      setNotification({ open: true, message: 'Login successful! Redirecting...', severity: 'success' });
      setTimeout(() => navigate('/dashboard'), 2000);
    } catch (error) {
      setNotification({
        open: true,
        message: error.response?.data?.message || 'Error logging in. Forgot your password?',
        severity: 'error'
      });
    } finally {
      setLoading(false);
    }
  };

  const handleNotificationClose = () => {
    setNotification({ ...notification, open: false });
  };

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: isDesktop ? 'row' : 'column',
        alignItems: 'center',
        justifyContent: 'center',
        height: 'calc(100vh - 64px)',
        px: 3,
        py: 6,
        bgcolor: 'background.default',
      }}
    >
      {isDesktop && (
        <Box
          component="img"
          src="/images/dental-login.jpeg"
          alt="Dental Clinic"
          sx={{
            width: '50%',
            maxHeight: 'calc(80vh - 64px)',
            objectFit: 'cover',
            borderRadius: 2,
            boxShadow: 3,
          }}
        />
      )}
      <Box
        sx={{
          maxWidth: 400,
          mx: 'auto',
          p: 3,
          bgcolor: 'background.paper',
          boxShadow: 3,
          borderRadius: 2,
          mt: isDesktop ? 0 : 4,
        }}
      >
        <Typography variant="h4" gutterBottom>
          Log In
        </Typography>
        <Button
          onClick={() => setUsePhone(!usePhone)}
          sx={{ mb: 2 }}
          color="primary"
          variant="text"
        >
          {usePhone ? 'Use Email Instead' : 'Use Phone Instead'}
        </Button>
        <form onSubmit={handleSubmit(onSubmit)}>
          {usePhone ? (
            <PhoneInput
              country={'us'}
              inputProps={{
                name: 'phone',
                required: true,
                autoFocus: true
              }}
              containerStyle={{ marginBottom: '16px' }}
              inputStyle={{ width: '100%' }}
              onChange={(phone) => setValue('phone', phone)}
            />
          ) : (
            <TextField
              label="Email"
              variant="outlined"
              fullWidth
              margin="normal"
              {...register('email', { required: 'Email is required' })}
              error={!!errors.email}
              helperText={errors.email ? errors.email.message : ''}
            />
          )}
          <TextField
            label="Password"
            type={showPassword ? 'text' : 'password'}
            variant="outlined"
            fullWidth
            margin="normal"
            {...register('password', { required: 'Password is required' })}
            error={!!errors.password}
            helperText={errors.password ? errors.password.message : ''}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton onClick={handleClickShowPassword} edge="end">
                    {showPassword ? <VisibilityOff /> : <Visibility />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
          <Button
            type="submit"
            variant="contained"
            color="primary"
            fullWidth
            sx={{ mt: 2 }}
            disabled={loading}
          >
            {loading ? <CircularProgress size={24} color="inherit" /> : 'Log In'}
          </Button>
        </form>

        <Button
          color="secondary"
          fullWidth
          sx={{ mt: 2 }}
          onClick={() => navigate('/reset-password')}
        >
          Forgot Password?
        </Button>

        <Button
          color="secondary"
          fullWidth
          sx={{ mt: 2 }}
          onClick={() => navigate('/signup')}
        >
          Don't have an account? Sign Up
        </Button>

        <Notification
          open={notification.open}
          message={notification.message}
          severity={notification.severity}
          onClose={handleNotificationClose}
        />
      </Box>
    </Box>
  );
};

export default LoginPage;
